/*
=============== 
Variables
===============
*/

:root {
  --clr-black: #0B090A;
  --clr-off-black: #161A1D;
  --clr-white: #FFFFFF;
  --clr-off-white: #F5F3F4;
  --clr-grey-1: #B1A7A6;
  --clr-grey-2: #D3D3D3;
  --clr-primary-1: #660708;
  --clr-primary-2: #A4161A;
  --clr-primary-3: #BA181B;
  --clr-primary-4: #E5383B;
  --clr-primary-3-transparent: rgba(186, 24, 27, 0.35);
  --transition: all 0.3s linear;
  --spacing: 0.1rem;
  --radius: 0.25rem;
  --light-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
  --dark-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
  --max-width: 1170px;
  --fixed-width: 620px;
}

/*
=============== 
Global Styles
===============
*/

*,
::after,
::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  background: var(--clr-off-black);
  color: var(--clr-grey-2);
  line-height: 1.5;
  font-size: 0.875rem;
}

ul {
  list-style-type: none;
}

a {
  text-decoration: none;
}

hr {
  border: none;
  border-top: 1px solid var(--clr-grey-1);
}

h1,
h2,
h3,
h4,
h5 {
  letter-spacing: var(--spacing);
  text-transform: capitalize;
  line-height: 1.25;
  margin-bottom: 0.75rem;
}

h1 {
  font-size: 2.5rem;
}

h2 {
  font-size: 2rem;
}

h3 {
  font-size: 1.5rem;
}

h4 {
  font-size: 1.25rem;
}

h5 {
  font-size: 0.875rem;
}

p {
  margin-bottom: 1.25rem;
  color: var(--clr-grey-1);
}

@media screen and (min-width: 800px) {
  h1 {
    font-size: 3rem;
  }

  h2 {
    font-size: 2.5rem;
  }

  h3 {
    font-size: 2rem;
  }

  h4 {
    font-size: 1.5rem;
  }

  h5 {
    font-size: 1rem;
  }

  body {
    font-size: 1rem;
  }

  h1,
  h2,
  h3,
  h4 {
    line-height: 1;
  }
}

/* global classes */

/* section */
.section {
  padding: 4rem 0;
}

.section-center {
  width: 90vw;
  margin: 0 auto;
  max-width: var(--max-width);
}

@media screen and (min-width: 992px) {
  .section-center {
    width: 95vw;
  }
}

.text-center {
  text-align: center;
}

@keyframes spinner {
  to {
    transform: rotate(360deg);
  }
}

.loading {
  width: 6rem;
  height: 6rem;
  margin: 0 auto;
  margin-top: 10rem;
  border-radius: 50%;
  border: 4px solid var(--clr-grey-3);
  border-top-color: var(--clr-primary-4);
  animation: spinner 0.9s ease-in-out infinite;
}

.btn {
  background: transparent;
  text-transform: uppercase;
  color: var(--clr-grey-2);
  padding: 1.25rem 1.75rem;
  letter-spacing: var(--spacing);
  font-weight: 600;
  transition: var(--transition);
  font-size: 0.875rem;
  cursor: pointer;
  border-radius: var(--radius);
  border: 2px solid var(--clr-primary-3);
}

.btn:hover {
  color: var(--clr-off-white);
  box-shadow: inset 0 0 0 1.75rem var(--clr-primary-3);
}

.title {
  text-align: center;
  margin-bottom: 5rem;
}

.title .underline {
  width: 6rem;
  height: 0.25rem;
  background: var(--clr-primary-3);
  margin-left: auto;
  margin-right: auto;
}

.page-100 {
  min-height: calc(100vh - 10rem);
  padding: 5rem 0;
}

.page {
  min-height: calc(100vh - (20vh + 10rem));
}

/* stack section */

.stack {
  margin-bottom: 1rem;
}

.stack span {
  display: inline-block;
  background: rgba(11, 9, 10, 0.3);
  color: var(--clr-grey-2);
  margin: 0.25rem;
  padding: 0.25rem 0.5rem;
  border-radius: var(--radius);
  text-transform: uppercase;
  letter-spacing: 2px;
  font-size: 0.85rem;
}

/* Navbar */

.nav-hide {
  top: -5rem !important;
}

.nav-scrolled {
  background: rgba(22, 26, 29, 0.8) !important;
  box-shadow: 0px 25px 20px -20px rgba(0, 0, 0, 0.45);
  backdrop-filter: blur(10px);
}